/* - Padding/Margin */
.no-padding {
	padding: 0;
}
.no-left-padding{
	padding-left: 0;
}
.no-right-padding {
	padding-right: 0;
}
.no-top-padding {
	padding-top: 0;
}
.no-bottom-padding {
	padding-bottom: 0;
}
.no-margin {
	margin: 0;
}
.no-left-margin {
	margin-left: 0;
}
.no-right-margin {
	margin-right: 0;
}
.no-top-margin {
	padding-top: 0;
}
.no-bottom-margin {
	margin-bottom: 0;
}
.margin-top20 {
	margin-top: 20px;
}
.margin-top30 {
	margin-top: 30px;
}
.margin-bottom30 {
	margin-bottom: 30px;
}

/* -- Section Padding */
.section-padding {
	padding-bottom: 75px;
	padding-top: 75px;
}
.section-padding40 {
	padding-bottom: 20px;
	padding-top: 20px;
}
.section-padding30 {
	padding-bottom: 15px;
	padding-top: 15px;
}
/* ## Header */
.header-main {
	border-top: 3px solid #feb005;
}
.top-header {
	padding-bottom: 26px;
	padding-top: 26px;
}
.top-header ul {
	padding-left: 0;
	margin-bottom: 34px;
	margin-top: 34px;
}
.top-header .social ul li {
	margin-right: 3px;
}
.top-header ul li,
.top-header ul li a {
	display: inline-block;
}
.top-header ul li a {
	width: 42px;
	height: 42px;
	line-height: 42px;
	color: #d2d2d2;
	border: 1px solid #d2d2d2;
	border-radius: 100%;
	text-align: center;
 transition: all 1s ease 0s;
}
.top-header ul li a:hover {
	color: #1A75B6;
	border-color: #1A75B6;
}
.top-header .add-to-cart {
	float: right;
}
.top-header .add-to-cart ul {
	text-align: right;
}
.top-header .add-to-cart ul li {
	margin-left: 20px;
}
.top-header .add-to-cart ul li a {
	position: relative;
}
.top-header .add-to-cart ul li a span {
	font-size: 11px;
	font-family: 'Roboto';
	color: #fff;
	width: 22px;
	height: 21px;
	background-color: #1f8bf3;
	display: inline-block;
	line-height: 21px;
	border-radius: 50%;
	position: absolute;
	right: -15px;
	top: -5px;
}
.top-header .logo-block {
	text-align: center;
}
.menu-block .container {
	position: relative;
}
/* ## Footer */
.footer-main {
	background-image: url(${'./images/ftr-bg.jpg'});
	background-repeat: no-repeat;
	background-size: cover;
	position: relative;
	padding-top: 95px;
}
.footer-main::before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: rgba(0,0,0,0.92);
	display: inline-block;
}
.ftr-widget,
.ftr-widget p a {
	color: #969595;
}
.ftr-widget .widget-title {
	color: #fff;
	font-family: 'Roboto';
	font-weight: 500;
	font-size: 17px;
	text-transform: uppercase;
	letter-spacing: 1.7px;
	margin-top: 0;
	margin-bottom: 15px;
}
.ftr-widget .widget-title span,
.ftr-widget p a,
.ftr-widget p span {
	display: block;
}
.ftr-widget .widget-title i  {
	height: 30px;
	display: inline-block;
}
.ftr-widget .widget-title span {
	margin-top: 23px;
}
.ftr-widget p {
	line-height: 28px;
	font-family: 'Roboto';
	font-size: 15px;
	letter-spacing: 0.45px;
}
.ftr-widget p a {
	text-decoration: none;
}
.ftr-widget p a:hover {
	color: #fff;
}
.ftr-widget.about-widget,
.ftr-widget.newsleater-widget {
	padding-bottom: 55px;
	padding-top: 68px;
	margin-top: 50px;
	border-top: 1px solid rgba(255,255,255,0.15);
}
.ftr-widget.about-widget {
	border-right: 1px solid rgba(255,255,255,0.15);
	padding-right: 25px;
	padding-left: 0;
}
.ftr-widget.about-widget p {
	margin-bottom: 0;
}
.ftr-widget.newsleater-widget .search-box .input-group .form-control {
	background-color: rgba(255,255,255,0.10);
	border-radius: 0;
	height: 40px;
	padding: 11px 18px;
	width: 98%;
}
.ftr-widget.newsleater-widget .search-box .input-group .btn-default {
	background-color: #1f8bf3;
	color: #fff;
	border-radius: 0;
	padding: 9px 14px;
	font-size: 15px;
	text-transform: uppercase;
	font-family: 'Roboto';
}
.ftr-widget.newsleater-widget .search-box .input-group .btn-default:hover {
	background-color: #fff;
	color: #1f8bf3;
}
.ftr-widget .search-box .input-group .form-control::-webkit-input-placeholder {
    color: #909090;
}
.ftr-widget .search-box .input-group .form-control:-moz-placeholder {
    color: #909090;
}
.ftr-widget .search-box .input-group .form-control::-moz-placeholder {
    color: #909090;
}
.ftr-widget .search-box .input-group .form-control:-ms-input-placeholder {
    color: #909090;
}
.footer-bottom {
	border-top: 1px solid rgba(255,255,255,0.15);
	padding: 34px 0;
}
.footer-bottom .copyright p {
	color: #909090;
	font-size: 15px;
}
.footer-bottom ul  {
	text-align: right;
	margin-bottom: 0;
	padding-left: 0;
}
.footer-bottom ul li {
	margin-left: 2px;
}
.footer-bottom ul li,
.footer-bottom ul li a {
	display: inline-block;
}
.footer-bottom ul li a {
	width: 35px;
	height: 35px;
	text-align: center;
	background-color: #393939;
	line-height: 35px;
	color: #909090;
}
.footer-bottom ul li a:hover {
	color: #393939;
	background-color: #909090;
}

/* -- Section Header */
.section-header {
	text-align: center;
	margin-bottom: 68px;
	background-image: url(${'./images/icon/section-separator.png'});
	background-repeat: no-repeat;
	background-position: center bottom;
	padding-bottom: 30px;
	margin-top: -5px;
}
.section-header h3 {
	font-family: 'Poppins';
	font-weight: 600;
	font-size: 38px;
	letter-spacing: 1.14px;
	color: #111;
	margin-top: 0;
	text-transform: uppercase;
	margin-bottom: 18px;
}
.section-header p {
	font-family: 'Roboto';
	font-size: 15px;
	letter-spacing: 1.2px;
	font-style:italic;
	color: #888;
	margin-bottom: 0;
}

/* ## Page Banner */
.page-banner {
    background-image: url(${'./images/banner/about-banner.jpg'});
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    color: #ffffff;
    max-height: 339px;
    min-height: 339px;
    position: relative;
    text-align: center;
    z-index: 1;
}
.page-banner::before {
	background-color: #000000;
    bottom: 0;
    content: "";
    display: inline-block;
    left: 0;
    opacity: 0.749;
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;
}

.services-banner {
	background-image: url(${'images/banner/services-banner.jpg'});
}
.gallery-banner {
	background-image: url(${'images/banner/gallery-banner.jpg'});
}
.contact-banner {
	background-image: url(${'images/banner/contact-banner.jpg'});
}
.pricing-banner {
	background-image: url(${'images/banner/pricing-banner.jpg'});
}
.faq-banner {
	background-image: url(${'images/banner/faq-banner.jpg'});
}
.error-banner {
	background-image: url(${'images/banner/404-banner.jpg'});
}

.page-banner .page-banner-content {
	padding-top: 82px;
	text-align: center;
}
.page-banner .page-banner-content h3 {
	color: #ffffff;
    display: inline-block;
    font-family: "Poppins",sans-serif;
    font-size: 38px;
    font-weight: 600;
    margin-bottom: 20px;
    margin-top: 0;
    text-transform: uppercase;
}
.page-banner .page-banner-content p {
    color: rgba(255, 255, 255, 0.6);
    font-family: "Roboto";
    font-size: 16px;
    line-height: 26px;
    margin-bottom: 0;
}
.page-banner .banner-content {
    background-color: rgba(32,110,186,0.749);
    bottom: 0;
    left: 0;
    padding: 13px 0;
    position: absolute;
    right: 0;
}
.page-banner .banner-content h4 {
    color: #ffffff;
    font-family: 'Roboto';
    font-size: 14px;
    letter-spacing: 1.12px;
    margin-bottom: 0;
    margin-top: 3px;
    text-transform: uppercase;
	font-weight: 400;
}
.page-banner .banner-content .breadcrumb {
	background-color: transparent;
    border-radius: 0;
    margin-bottom: 0;
    padding: 0;
}
.page-banner .banner-content .breadcrumb li,
.page-banner .banner-content .breadcrumb li a {
	color: #ffffff;
    font-family: 'Roboto';
    font-size: 16px;
    text-decoration: none;
    text-transform: capitalize;
}
.page-banner .banner-content .breadcrumb > li + li::before {
	color: #fff;
}

/* Pagination */
.ow-pagination {
	display: inline-block;
	width: 100%;
	text-align: center;
	margin-top: 90px;
}
.blog-list .ow-pagination {
	margin-top: 50px;
}
.ow-pagination .pagination {
	margin: 0;
}
.ow-pagination .pagination li {
	float: left;
	margin-right: 10px;
}
.ow-pagination .pagination a {
	font-size: 15px;
	padding: 9px 17px;
	font-family: 'Work Sans';
