label>[type=radio] + p {
    text-align: center;
    font-size: 20px;
}

/* CHECKED STYLES */
label>[type=radio]:checked + p ~ img {
  border: 2px solid #feb005;
  border-radius: 10px;
  padding: 2px;
}

