	padding-bottom: 26px;
	padding-top: 26px;
}
.top-header ul {
	padding-left: 0;
	margin-bottom: 34px;
	margin-top: 34px;
}
.top-header .social ul li {
	margin-right: 3px;
}
.top-header ul li,
.top-header ul li a {
	display: inline-block;
}
.top-header ul li a {
	width: 42px;
	height: 42px;
	line-height: 42px;
	color: #d2d2d2;
	border: 1px solid #d2d2d2;
	border-radius: 100%;
	text-align: center;
 transition: all 1s ease 0s;
}
.top-header ul li a:hover {
	color: #1A75B6;
	border-color: #1A75B6;
}
.top-header .add-to-cart {
	float: right;
}
.top-header .add-to-cart ul {
	text-align: right;
}
.top-header .add-to-cart ul li {
	margin-left: 20px;
}
.top-header .add-to-cart ul li a {
	position: relative;
}
.top-header .add-to-cart ul li a span {
	font-size: 11px;
	font-family: 'Roboto';
	color: #fff;
	width: 22px;
	height: 21px;
	background-color: #1f8bf3;
	display: inline-block;
	line-height: 21px;
	border-radius: 50%;
	position: absolute;
	right: -15px;
	top: -5px;
}
.top-header .logo-block {
	text-align: center;
}
.menu-block .container {
	position: relative;
}
/* ## Footer */
.footer-main {
	background-image: url(${'./images/ftr-bg.jpg'});
	background-repeat: no-repeat;
	background-size: cover;
	position: relative;
	padding-top: 95px;
}
.footer-main::before {
	content: "";
