/* - Map */
.map{
	position: relative;
}
.map-canvas {
    height: 550px;
    width: 100%;
	display: inline-block;
}
.map-content {
	max-width: 253px;
	display: block;
	margin: 0 auto;
	background-color: #fff;
	height: 282px;
	position: relative;
	padding: 13px 30px 13px;
}
.map-content::before {
	content: "";
	position: absolute;
	left: 5px;
	right: 5px;
	top: 5px;
	bottom: 5px;
	border: 1px solid #1f8bf3;
	background-color: #fff;
	z-index: 1;
}
.map-content::after {
	content: "";
	position: absolute;
	bottom: 0;
	right: 0;
	width: 47px;
	height: 47px;
	border-bottom: 47px solid #1f8bf3;
	border-left: 47px solid transparent;
	z-index: 0;
}
.map-content p {
	position: relative;
	z-index: 1;
	text-align: left;
	margin-top: 30px;
	margin-bottom: 0;
	padding-left: 40px;
	font-family: 'Roboto';
	font-size: 13px;
	font-weight: 300;
	line-height: 24px;
}
.map-content p i {
	position: absolute;
	width: 24px;
	left: 0;
	text-align: center;
	top: 50%;
	-webkit-transform: translate(0, -50%);
    transform: translate(0, -50%);
}
.map-content p a {
	display: block;
	text-decoration: none;
}
.map-content p,
.map-content p a {
	color: #777;
}
.map-content p a:hover {
	color: #1f8bf3
}

/* -- Contact form */
.contact-us {
	text-align: center;
}
.contact-us .form-control {
	border-radius: 0;
	box-shadow: none;
	min-height: 40px;
	border-color: #ececec;
	box-shadow: none;
	font-family: 'Roboto', sans-serif;
	font-size: 13px;
	padding: 6px 16px;
	letter-spacing: 0.52px;
}
.contact-us .form-control::-webkit-input-placeholder {
   color: #d1d1d1;
}
.contact-us .form-control:-moz-placeholder { /* Firefox 18- */
   color: #d1d1d1;
}
.contact-us .form-control::-moz-placeholder {  /* Firefox 19+ */
   color: #d1d1d1;
}
.contact-us .form-control:-ms-input-placeholder {
   color: #d1d1d1;
}
.contact-us .form-control:focus {
	border-color: #ececec;
}
.contact-us textarea {
	display: inline-block;
	height: 170px;
	margin-bottom: 20px;
}
.contact-us input[type="submit"] {
	color: #fff;
	font-size: 14px;
	display: inline-block;
	font-weight: 700;
	font-family: 'Roboto', sans-serif;
	background-color: #1f8bf3;
	border: 1px solid #1f8bf3;
	padding: 10px 32px;
	letter-spacing: 1.12px;
	text-transform: uppercase;
	border-radius: 0;
	text-decoration: none;
 transition: all 1s ease 0s;
}
.contact-us input[type="submit"]:hover {
	color: #1f8bf3;
	background-color: #fff;
	border: 1px solid #1f8bf3;
}

